import styled from 'styled-components';
import { ProfileImgXS } from './Img/ProfileImg';
import img from '../../images/pf_sample.png';
import { Link } from 'react-router-dom';
import { frontStacks, backStacks } from '../../StackData';

export type communityDataType = {
  startDate: string;
  dateCreated: string;
  id: string;
  nickName: string;
  title: string;
  haveHeadCount: string;
  totalHeadCount: string;
  devStack: [];
  devType: string;
  onOff: string;
};

interface Props {
  data: communityDataType;
  idx: number;
}

export const StudyCard = ({ data }: Props) => {
  const startDay = new Date(data.startDate);
  const toDay = new Date();
  const diff = +startDay - +toDay;
  const diffDay = Math.floor(diff / (1000 * 60 * 60 * 24));

  // 모집마감일 디데이
  function diffDayData() {
    if (diffDay <= 0) {
      return '종료';
    }
    return `D-${diffDay}`;
  }

  const devStackWord = (devType: string, skill: string) => {
    let a = '';
    let idx = 0;
    if (devType === 'frontend') {
      idx = frontStacks.findIndex((el) => el === skill);
      a = 'fe';
    }

    if (devType === 'backend') {
      idx = backStacks.findIndex((el) => el === skill);
      a = 'be';
    }

    if (idx >= 9) {
      return `sk_${a}${idx + 1}`;
    } else {
      return `sk_${a}${idx + 1}`;
    }
  };

  return (
    <>
      <StyledLink to={`/studyjoin/detail/${data.id}`}>
        <ItemCard>
          <ProjectInfo>
            <div className="date-area">
              <span className="date-area__date">
                <em>{diffDayData()}</em> {data.startDate}
              </span>
              <ThumbnailGroup>
                <li>
                  <ProfileImgXS src={img} />
                </li>
                <li>
                  <ProfileImgXS src={img} />
                </li>
                <li>+2</li>
              </ThumbnailGroup>
            </div>
            <h4>{data.title}</h4>
            <ul className="skill-area">
              {data.devStack.map((el, idx) => {
                return (
                  <li key={idx}>
                    <img
                      src={
                        process.env.PUBLIC_URL +
                        `/skill/${devStackWord(data.devType, `${el}`)}.png`
                      }
                      alt=""
                    />
                  </li>
                );
              })}
            </ul>
            <ul className="meta-area">
              <li>#{data.onOff === 'on' ? '온라인' : '오프라인'}</li>
              <li>#3개월</li>
            </ul>
          </ProjectInfo>
          <StudyInfo>
            <p>
              {data.nickName}
              <em>
                {diffDay <= 0 || data.haveHeadCount === data.totalHeadCount
                  ? '모집 종료'
                  : '모집 중'}{' '}
                {data.haveHeadCount}/{data.totalHeadCount}명
              </em>
            </p>
          </StudyInfo>
        </ItemCard>
      </StyledLink>
    </>
  );
};

const ItemCard = styled.div`
  display: block;
  background: #fff;
  border-radius: 25px;
  border: 1px solid #ededed;
  padding: 25px 25px;
  cursor: pointer;

  &:hover {
    border-color: #e1e1e1;
    box-shadow: 0 8px 10px 10px rgba(0, 0, 0, 0.03);
  }
`;

const ProjectInfo = styled.div`
  position: relative;
  display: block;
  padding-bottom: 35px;
  min-height: 225px;
  .date-area {
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    font-family: 'Pretendard-medium';

    .date-area__date {
      em {
        display: inline-block;
        padding: 5px 9px;
        background: #000;
        border-radius: 50px;
        font-weight: bold;
        color: #fff;
      }
    }
  }
  h4 {
    padding: 12px 0 0;
    font-size: 21px;
    font-family: 'Pretendard-Medium';
    line-height: 1.2em;
    letter-spacing: -0.025em;
    min-height: 77px;
  }
  .skill-area,
  .meta-area {
    display: flex;
    gap: 5px;
    flex-wrap: wrap;
  }
  .skill-area {
    li {
      width: 35px;
      height: 35px;

      img {
        width: 100%;
      }
    }
  }
  .meta-area {
    position: absolute;
    bottom: 13px;
    gap: 8px;
    li {
      font-family: 'Pretendard-Medium';
      font-size: 15px;
      color: #75787e;
    }
  }
`;

const StudyInfo = styled.div`
  display: block;
  border-top: 1px solid #ededed;
  padding: 15px 0 0;
  font-size: 17px;
  font-weight: bold;
  line-height: 1.3em;
  em {
    display: block;
    font-size: 15px;
    font-family: 'Pretendard-medium';
    font-weight: normal;
  }
`;

const ThumbnailGroup = styled.ul`
  display: flex;
  li {
    margin-left: -8px;
    font-size: 12px;
    &:last-child {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      border: 1px solid #696969;
      border-radius: 50px;
      width: 27px;
      height: 27px;
    }
  }
`;

const StyledLink = styled(Link)`
  text-decoration: none;
  color: black;
`;
